import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { trigger,style,transition, animate,keyframes,query,stagger} from '@angular/animations';
import { DataService } from '../data.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations:[
    trigger('goals',[
      transition('* => *', [
        query(':enter',style({opacity:0}),{optional:true}),

        query(':enter',stagger('300ms',[
          animate('.6s ease-in', keyframes([
            style({opacity: 0, transform: 'translateY(-75%)',offset: 0}),
            style({opacity: .5, transform: 'translateY(35px)',offset: .3}),
            style({opacity: 1, transform: 'translateY(0)',offset: 1}),
          ]))
        ]),{optional:true}),

        query(':leave',stagger('300ms',[
          animate('.6s ease-in', keyframes([
            style({opacity: 1, transform: 'translateY(0)',offset: 0}),
            style({opacity: .5, transform: 'translateY(35px)',offset: .3}),
            style({opacity: 0, transform: 'translateY(-75%)',offset: 1}),
          ]))
        ]),{optional:true})
      ])
    ])
  ]
})
export class HomeComponent implements OnInit {
//interpolation= the insertion of something of a different nature into something else.
//PROPERTIES
itermCount: number =4;// interpolation
tbnText: string ='Add an item';// propery binding 
goalText: string ="My first life Goal";// 2 way bining 
 goals=[]//Declare array event Binding 

  constructor( private _data: DataService) { }

  ngOnInit() {
    this._data.goal.subscribe(res => this.goals = res);
    this.itermCount=this.goals.length;
    this._data.changeGoal(this.goals);
  }

  addItem(){
    this.goals.push(this.goalText);
    this.goalText='';
    this.itermCount = this.goals.length;
    
  }

  removeItem(i){
    this.goals.splice(i,1);
  }
}



